import React from 'react';
import {Row,Col} from 'antd';
import {Link} from 'react-router-dom'

const CategoriesDisplay = ({categories=[]}) =>{
    return <section id='products' className='products'>
        <Row className='title'>
            <Col span={7}>
                <div className='divider'/>
            </Col>
            <Col span={10}>
                <span className='aboutTitle'>Productos</span>
            </Col>
            <Col span={7}>
                <div className='divider'/>
            </Col>
        </Row>
        <Row className='productsList'>
            <div className="grid">
                {categories.map((category, key)=>{
                    return <Link to={`/categoryDetail/${category.id}`} key={key}><figure className="effect-ruby">
                        <img onError={(e)=>{e.target.onerror = null; e.target.src=null}} src={`/images/categorias${category.imagePath}`} alt='CategoryImg' className='categoryImage'/>
                        <figcaption>
                            <h2><span>{category.name}</span></h2>
                            <p>{category.description}</p>
                        </figcaption>
                    </figure></Link>
                })}
            </div>
        </Row>
    </section>

};

export default CategoriesDisplay;