import React from 'react';
import {Modal,Form,Input,Radio,DatePicker,TimePicker} from "antd";
import moment from 'moment';
const RadioGroup = Radio.Group;
const { RangePicker } = DatePicker;


const EventFormDisplay = ({onCancel,
                              handleAddEvent,
                              handleEditEvent,
                              handleCancelAdding,
                              onInputChange,
                              onChangeRadio,
                              onChangeDate,
                              onChangeTime,
                              radioSelected,
                              action,
                              form}) =>{
    const radioStyle = {
        display: 'block',
        height: '30px',
        lineHeight: '30px',
    };

    const now = moment();
    return(
        <Modal
            className='Modals'
            title={action==='add'?'Agregar evento':'Editar evento'}
            visible={true}
            width={400}
            okText={action==='add'?'Agregar':'Guardar'}
            cancelText='Cancelar'
            okButtonProps={{shape:'round'}}
            cancelButtonProps={{shape:'round'}}
            onOk={action==='add'?handleAddEvent:handleEditEvent}
            onCancel={onCancel}>
            <Form layout="vertical" onSubmit={action==='add'?handleAddEvent:handleEditEvent}>
                <Form.Item label="Nombre:">
                    <Input onChange={onInputChange} name='name' autoComplete='off' autoFocus={true} value={form?form.name:''}/>
                </Form.Item>
                <Form.Item label="Descripción:">
                    <Input onChange={onInputChange} type="textarea" autoComplete='off' name='description' value={form?form.description:''} />
                </Form.Item>
                <Form.Item label="Lugar:">
                    <Input onChange={onInputChange} name='place' autoComplete='off' value={form?form.place:''}/>
                </Form.Item>
                <Form.Item label="Fecha:">
                    <RadioGroup onChange={onChangeRadio} value={form?!!form.singleDay:true}>
                        <Radio style={radioStyle} value={true}>1 día</Radio>
                            <DatePicker size={"default"} disabled={form?(form.singleDay===true?false:true):false} value={form?form.startDate:moment(now,"DD-MM-YYYY")} onChange={onChangeDate} name='startDate' format={"DD-MM-YYYY"} />
                        <Radio style={radioStyle} value={false}>Rango de días</Radio>
                            <RangePicker size={"default"} disabled={form?!!form.singleDay:false} value={form? [moment(form.startDate,"DD-MM-YYYY"), moment(form.endDate,"DD-MM-YYYY")]:[moment(now, "DD-MM-YYYY"), moment(now.add(1,'days'), "DD-MM-YYYY")]} format={"DD-MM-YYYY"} onChange={onInputChange} name='endDate'/>
                    </RadioGroup>
                </Form.Item>
                <Form.Item label="Horario:">
                    De: <TimePicker onChange={onInputChange} defaultOpenValue={moment('00:00:00', 'HH:mm:ss')} value={form?form.startTime:moment('00:00:00', 'HH:mm:ss')} name='startTime'/>
                    a: <TimePicker onChange={onInputChange} defaultOpenValue={moment('00:00:00', 'HH:mm:ss')} value={form?form.endTime:moment('00:00:00', 'HH:mm:ss')} name='endTime'/>
                </Form.Item>
            </Form>
        </Modal>
    )

};

export default EventFormDisplay;