import React from 'react';
import {Button,Modal,Row,Col} from "antd";
import EventFormContainer from "./components/EventFormContainer";

const AdminEventDisplay = ({events=[],
                                  onAddEvent,
                                  onEditEvent,
                                  onDeleteEvent,
                                  addingEvent=false,
                                  editingEvent=null,
                                  detailingEvent=false,
                                  deletingEvent=false,
                                  handleCancelAdding,
                                  handleCancelEditing,
                                  handleDeleteEvent,
                                  onCancelDeleteEvent}) =>{

    return(
        <div className='AdminEventDisplay'>
            <p>Lista de Eventos</p>
            <div className='buttonAddContainer'>
                <Button className='buttonAdd' shape='round' type="primary" icon="plus" onClick={onAddEvent}>Agregar</Button>
            </div>

            <section>
                <div className="eventsTable">
                    <Row className='headerTable'>
                        <Col span ={18}>Nombre</Col>
                        <Col span={6}>Acciones</Col>
                    </Row>
                    {events.map((event, key)=>{
                        return <Row className='event' key={key}>
                            <Col span ={18}>{event.name}</Col>
                            <Col span={6}><i className="fas fa-pen" onClick={()=>onEditEvent(event)}/><i className="fas fa-trash" onClick={()=>onDeleteEvent(event)}/></Col>
                        </Row>
                    })}
                </div>
            </section>

            {/*Modal para Agregar evento*/}
            {addingEvent&& <EventFormContainer
                action='add'
                cancel={handleCancelAdding}>
            </EventFormContainer>}

            {/*Modal para Editar evento*/}
            {editingEvent&& <EventFormContainer
                action='edit'
                editingEvent={editingEvent}
                cancel={handleCancelEditing}>
            </EventFormContainer>}

            {/*Modal para Eliminar evento*/}
            {deletingEvent&&<Modal
                className='Modals'
                title='Eliminar categoría'
                visible={true}
                width={350}
                okText='Eliminar'
                cancelText='Cancelar'
                okButtonProps={{shape:'round'}}
                cancelButtonProps={{shape:'round'}}
                onOk={handleDeleteEvent}
                onCancel={onCancelDeleteEvent}>
                <p>¿Realmente deseas borrar el evento <b>{deletingEvent.name}</b>?</p>
            </Modal>}
        </div>
    )

};

export default AdminEventDisplay;