import React, {Component} from 'react';
import EventFormDisplay from '../components/EventFormDisplay'
import firebase from '../../../../firebase'
import toastr from 'toastr';

class EventFormContainer extends Component{

    constructor(props){
        super(props);
        this.props = props;
        this.eventsRef = firebase.database().ref('event');
        if(props.action==='edit'){
            this.state={
                form:{
                    name:props.editingEvent.name,
                    description:props.editingEvent.description,
                    place:props.editingEvent.place,
                    singleDay:props.editingEvent.place,
                    startDate:props.editing.startDate,
                    endDate:props.editing.endDate,
                    startTime:props.editing.startTime,
                    endTime:props.editing.endTime
                }
            }
        }
        else this.state = {
            form:{
                singleDay:true
            }
        };
    }

    handleAddEvent = (e) =>{
        e.preventDefault();
        const newEvent = this.state.form;
        this.eventsRef.push(newEvent);
        this.props.cancel();
    };

    handleEditEvent = (e)=>{
        e.preventDefault();
        const idEvent = this.props.editingEvent.id, updatedEvent = this.state.form;
        this.eventsRef.child(idEvent).update(updatedEvent);
        this.props.cancel();
    }

    onInputChange = (e) => {
        console.log(e.target)
        this.setState({form:{...this.state.form,[e.target.name]:e.target.value}})};

    onChangeRadio = (e) => {
        console.log(e.target.value)
        this.setState({form:{...this.state.form,singleDay:e.target.value}},()=>console.log(this.state))
    };

    onChangeDate = (e,val)  => {
        console.log(e.currentTarget)
        //this.setState({form:{...this.state.form,[e.target.name]:e.target.value}})
    };

    render() {
        const {form} = this.state;
        return (
            <div>
                <EventFormDisplay
                    action={this.props.action}
                    handleAddEvent={this.handleAddEvent}
                    onInputChange={this.onInputChange}
                    onCancel={this.props.cancel}
                    handleEditEvent={this.handleEditEvent}
                    onChangeRadio={this.onChangeRadio}
                    onChangeDate={this.onChangeDate}
                    onChangeTime={this.onChangeTime}
                    form={form}
                />
            </div>
        );
    }
}

export default EventFormContainer;
