import React from 'react';
import {Row,Col} from 'antd';

const EventsDisplay = ({events=[]}) =>{
    return <section id='events' className='EventsDisplay'>
        <Row className='title'>
            <Row className='title'>
                <Col span={7}>
                    <div className='divider'/>
                </Col>
                <Col span={10}>
                    <span className='aboutTitle'>Eventos</span>
                </Col>
                <Col span={7}>
                    <div className='divider'/>
                </Col>
            </Row><br/>
            <Row className='eventsList'>
                {events.map((event, key)=>{
                    return <div key={key}>
                        <h2><span>{event.name}</span></h2>
                        <p>{event.description}</p>
                        <p>{event.place}</p>
                        <p>{event.date}</p>
                    </div>
                })}
            </Row>
        </Row>
    </section>
}

export default EventsDisplay;