import React, {Component} from 'react';
import AdminEventsDisplay from './AdminEventsDisplay'
import firebase from '../../../firebase'
import toastr from 'toastr';

class AdminEventsContainer extends Component{

    state={addingEvent: false,form:{}};

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);

        const eventsRef = firebase.database().ref('event');

        eventsRef.on('value', (snapshot) => {
            let events = snapshot.val();
            let eventsArray = [];
            for (let event in events) {
                eventsArray.push({
                    id: event,
                    name: events[event].name,
                    description:events[event].description,
                    place:events[event].place,
                    date:events[event].date
                });
            }
            this.setState({ events: eventsArray });
        });
    }

    addEvent = () => this.setState({addingEvent:true});

    editEvent = (event) => this.setState({editingEvent:event});

    deleteEvent = () =>{
        const eventsRef = firebase.database().ref(`/event/${this.state.deletingEvent.id}`);
        eventsRef.remove().then(()=>this.closeDeleteEvent())
    };

    handleDeleteEvent = (event) => this.setState({deletingEvent:event});

    closeAddEvent = () => this.setState({addingEvent:false});

    closeEditingEvent = () => this.setState({editingEvent:false});

    closeDeleteEvent = () => this.setState({deletingEvent:false});

    onInputChange = (e) => this.setState({form:{...this.state.form,[e.target.name]:e.target.value}});

    render() {
        const {events,addingEvent,editingEvent,deletingEvent}=this.state;
        return (
            <AdminEventsDisplay
                events={events}
                onAddEvent={this.addEvent}
                onEditEvent={(event)=>this.editEvent(event)}
                onDeleteEvent={(event)=>this.handleDeleteEvent(event)}
                addingEvent = {addingEvent}
                editingEvent={editingEvent}
                deletingEvent={deletingEvent}
                handleCancelEditing={this.closeEditingEvent}
                handleCancelAdding={this.closeAddEvent}
                handleDeleteEvent={(event)=>this.deleteEvent(event)}
                onCancelDeleteEvent={this.closeDeleteEvent}
                onInputChange={this.onInputChange}
            />
        );
    }
}

export default AdminEventsContainer;
